import { Router } from '@angular/router';
import { Oauth2Service } from './oauth2.service';
var OidpGuard = /** @class */ (function () {
    function OidpGuard(router, oauthService) {
        this.router = router;
        this.oauthService = oauthService;
    }
    OidpGuard.prototype.canActivate = function (route, state) {
        var valid = this.oauthService.hasValidToken();
        if (!valid) {
            this.router.navigate(['/oauth2']);
            return false;
        }
        return valid;
    };
    return OidpGuard;
}());
